import { getUsers } from '@/actions/user.actions'
import DataTableUsers from '../_components/datatable-users'
import { Button } from '@mui/material'
import Link from 'next/link'
import DataTableProducts from './_components/datatable-products'

const ProductPage = async () => {
  const users = await getUsers()

  return (
    <>
      <div className='page-header'>
        <h1 className='page-heading'>Products</h1>
        <Button variant='contained'>
          <Link href='/create'>Create Product</Link>
        </Button>
      </div>
      <DataTableProducts data={users.users} />
    </>
  )
}

export default ProductPage
